html {
    height: 100%;
  }

body {
  position: relative;
  margin: 0;
  padding-bottom: 6rem;
  min-height: 100%;
}

#footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 1rem;
    text-align: center;
    border-top: 1px solid gray;
}

#footer-item-container {
    margin: auto;
    padding: 5px;
    width: 300px;
    height: 30px;
}

.footer-item {
    margin: 20px;
}

.footer-image:hover {
    opacity: 0.8;
    cursor: pointer;
}
#menubar-container {
    width: 100%;
    height: 50px;
}

#menubar-item-container {
    padding: 30px;
    float: left;
    margin-left: 120px;
}

#menubar-item-list {
    display: flex;
}

#menubar-item {
    font-size: 20px;
    padding: 5px;
    margin: 50px;
    color: black;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    transition: 0.4s;
    opacity: 0.9;
    border-radius: 5px;   
}

#menubar-item:hover {
    opacity: 1;
    background-color: lightgray;
}

#menubar-item:focus {
    color: black;
}

#mobile-menu-button {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 4px;
    transition: 0.3s;
}

#mobile-menu-button:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

#mobile-menu-button:focus {
    outline: none;
}

#mobile-menubar-container {
    width: 100%;
    padding: 20px;
    display: none;
    position: fixed;
    text-align: left;
}

#mobile-menubar-item-container {
    padding: 10px;
    display: inline-block;
    position: absolute;
    left: 0px;
    top: 45px;
    transition: 0.3s;
}

#mobile-menubar-item-list {
    list-style: none; 
    padding-left: 0px;
    width: 120px; 
    text-align: left; 
    margin-top: 25px;
    box-shadow: 0px 5px 10px -4px rgba(0,0,0,0.52);
    background-color: white;
}

#mobile-menubar-item {
    font-size: 20px;
    padding: 5px;
    color: black;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    transition: 0.4s;
    opacity: 0.9;
    border-radius: 5px;   
}

#mobile-menubar-item:hover {
    opacity: 1;
}

#mobile-menubar-item:focus {
    color: black;
}

#mobile-menubar-list-item {
    padding: 20px;
    transition: 0.3s;
}

#mobile-menubar-list-item:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

.mobile-menu-line {
    width: 35px;
    height: 3px;
    background-color: black;
    margin: 6px 0;
    border-radius: 20px;
}

@media screen and (max-width: 830px) {
    #menubar-container {
        display: none;
    }

    #mobile-menubar-container {
        display: block;
    }
}
#home-body-container {
    width: 100%;
}

#visitor-container {
    display: flex;
    min-width: 600px;
    max-width: 700px;
    padding-top: 100px;
    margin: auto;
}

#visitor-textarea-container {
    text-align: left;
    font-family: Consolas, sans-serif;
    border: solid grey 1px;
    resize: none;
    height: 528px;
    width: 375px;
    outline: none;
    background-color: #363637;
    box-shadow: 0px 5px 10px -4px rgba(0,0,0,0.52);
}

#name-input {
    border: none;
    font-family: Consolas, sans-serif;
    margin-top: 10px;
    height: 20px;
    width: 60px;
    vertical-align: middle;
    background-color: #232323;
    color: #a5c261;
}

#name-input:focus {
    border: none;
    outline: none;
}

#visitor-name-container {
    width: 350px;
    word-wrap: break-word;    
}

#visitor-name {
    font-size: 60px;
    color: black;
    margin-top: 5px;
    padding: 20px;
    font-family: Varela Round, sans-serif;
}

@media screen and (max-width: 1030px) {
    #visitor-container {
        display: block;
        transition: 0.5s;
        margin: auto;
    }

    #code-area {
        height: 328px;
        width: 375px;
    }

    #visitor-textarea-container {
        margin: auto;
    }

    #visitor-name-container {
        margin: auto;
    }
}

@media screen and (max-width: 510px) {
    #visitor-textarea-container {
        margin-left: 20px;
    }

    #visitor-name-container {
        margin-left: 20px;
    }
}

@media screen and (max-width: 400px) {
    #visitor-textarea-container {
        margin-left: 10px;
    }

    #visitor-name-container {
        margin-left: 10px;
    }

    #visitor-textarea-container {
        width: 350px;
    }
}

@media screen and (max-width: 370px) {
    #visitor-textarea-container {
        width: 320px;
    }

    #visitor-name-container {
        margin-left: 5px;
    }
}
#about-container {
    width: 500px;
    margin: auto;
    padding: 100px;
    font-family: Varela Round, sans-serif;
}

#about-header {
    text-align: center;
}

#about-text {
    font-size: 16px; 
    text-align: left;
}

@media screen and (max-width: 610px) {
    #about-container {
        padding: 80px;
        width: 400px;
    }

    #about-text {
        font-size: 15px; 
    }
}

@media screen and (max-width: 480px) {
    #about-container {
        padding: 80px;
        width: 300px;
    }
}

@media screen and (max-width: 410px) {
    #about-container {
        padding: 80px;
        width: 250px;
        font-size: 14px;
    }

    #about-text {
        font-size: 14px; 
    }
}
#project-container {
    width: 100%;
    margin-top: 50px;
    font-family: Varela Round, sans-serif;
}

#project-item-container {
    width: 200px;
    height: 200px;
    background-color: transparent;
    padding: 5px;
    margin: 5px;
    border-radius: 4px;
    box-shadow: 0px 4px 7px -4px rgba(0,0,0,0.75);
    transition: 0.3s;
}

#project-item-container:hover {
    cursor: pointer;
    margin-top: 0px;
    box-shadow: 0px 4px 27px -10px rgba(0,0,0,0.75);
    transition: 0.3s;

}

#project-item-button {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 4px;
    transition: 0.3s;
}

#project-item-button:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

#project-item-button:focus {
    outline: none;
}

#project-item-link {
    text-decoration: none;
    color: #474646;
}

#project-button-container {
    height: 30px;
    padding-top: 50px;
    padding-right: 70px;
}

.project-item-image {
    width: 100%;
    height: 30%;
}

.project-item-desc {
    font-size: 16px;
    margin-bottom: 5px;
}

.project-items {
    width: 100%; 
    display: flex;
}

.items {
    display: flex;
    margin: auto;
}

@media screen and (max-width: 1150px) {
    .items {
        display: block;
        width: 500px;
    }

    #project-item-container {
        width: 100%;
    }

    .project-item-image {
        height: 60%;
    }
}

@media screen and (max-width: 600px) {
    .items {
        display: block;
        width: 300px;
    }
}

#skills-container {
    margin-top: 50px;
    margin-bottom: 100px;
    width: 1000px;
    margin-left: auto;
    margin-right: auto;
    font-family: Varela Round, sans-serif;
}
body {
    text-align: center;
}
